import { useState, useEffect } from 'react'
import fetch from 'isomorphic-unfetch'

const Profile = ({ avatarURL, profileURL, username }) => (
    <>
        <style jsx>{`
            img {
                width: 30px;
                height: 30px;
            }
        `}</style>
        <div className="inline-block bg-gray-100 px-4 py-3 m-2 rounded-lg">
            <div className="flex flex-row items-center">
                <img className="rounded-full mr-4" src={avatarURL} />
                <div className="text-sm text-gray-600">{username}</div>
            </div>
        </div>
    </>
)

const Members = () => {
    const [members, setMembers] = useState([])
    useEffect(() => {
        ;(async function fetchAPI() {
            const data = await fetch(`https://api.github.com/orgs/thinc-org/public_members`)
            setMembers(data)
            console.log(members)
        })()
    }, [])
    const renderProfiles = () => {
        if (members.length === 0) {
            return null
        } else {
            return (
                <>
                    {members.map((value, index) => {
                        return (
                            <Profile
                                username="UtopiaBeam"
                                avatarURL="https://avatars3.githubusercontent.com/u/15919822?v=4"
                                profileURL="https://api.github.com/users/UtopiaBeam"
                            />
                        )
                    })}
                </>
            )
        }
    }
    return (
        <section className="w-full bg-white py-20">
            <div className="container">
                <div className="mx-8">
                    <div className="w-full flex items-center flex-col">
                        <div className="w-full md:w-2/3 mb-20">
                            <p className="text-4xl text-center font-headline font-semibold leading-none text-black mb-10">
                                Members
                            </p>
                            <p className="text-base text-center font-normal leading-relaxed">
                                <span className="font-bold">Thinc.</span> is community consisted of
                                more than 100 members. All members displayed are public members from
                                our GitHub organization.
                            </p>
                        </div>
                        <div className="w-full flex justify-center">
                            <div className="inline-block text-center">{renderProfiles()}</div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Members
